<template>
    <div class="fault">
        
        <div>
          <div style="font-size:20px;font-weight:bold;height:50px;line-height:50px;width:48%;float:left;">故障管理</div>
            <div style="float:right;margin-right:10px;height:50px;line-height:50px;width:50%;text-align:right">
              <el-input v-model="fInput" placeholder="请输入设备ID" style="width:200px"></el-input><el-button type="info" @click="cheack">查询</el-button>
            </div>
          </div>
        <el-table
        :data="tableData"
        border
    style="width: 100%;">
    <el-table-column
      prop="fId"
      label="故障ID"
      width="180"
      >
    </el-table-column>
    <el-table-column
      prop="eId"
      label="设备id">
    </el-table-column>
    <el-table-column
      prop="sId"
      label="服务id">
    </el-table-column>
    <el-table-column
      prop="fType"
      label="故障类型">
    </el-table-column>

    <el-table-column
      prop="chargePerson"
      label="负责人">
    </el-table-column>

    <!-- <el-table-column
      prop="method"
      label="修复方法">
    </el-table-column> -->

    <el-table-column
      prop="result"
      label="处理结果">
    </el-table-column>

    <el-table-column
      prop="operateTime"
      label="执行时间">
    </el-table-column>

    <el-table-column
      prop="methods"
      label="操作"
      >
      <template slot-scope="scope" >

        <el-button
          size="small  "
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>

    </el-table-column>

    </el-table>




    </div>
</template>

<script>
export default {
    name:'Fault',
    data(){
        return{
            tableData:[],
            fInput:''
        }
    },
    methods:{
        //删除
        handleDelete(index,row){
            this.$confirm('此操作将永久删除, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$axios.delete('http://192.168.1.155:8888/FRController/'+row.fId).then((msg)=>{
              if (msg.data) {
                    this.$message({
                        message: '恭喜你，删除成功',
                        type: 'success'
                    });
                    window.location.reload()
                }else{
                    this.$message.error('删除失败');
                }
            })

          }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
            });

        },
        //查询故障
        cheack(){
          let obj = {
            'fId':this.fInput
          }
          if (!isNaN(this.fInput)) {
            this.$axios.post('http://192.168.1.155:8888/FRController',obj).then((msg)=>{
            if (!msg.data.length) {
              //没有数据
              this.$message({
                message: '没有查找到数据',
                type: 'warning'
              });
            }else{
              this.tableData = msg.data
            }
            })
          }else{
            this.$message({
              message: '请输入数字',
              type: 'warning'
            });
          }
          this.fInput = ''
        },
    },
    mounted(){
      this.$axios.get('http://192.168.1.155:8888/FRController').then((msg)=>{
        this.tableData = msg.data.slice(0,10)
      })
    }
}
</script>

<style scoped>

</style>


